.data {
  transition: opacity 0.5s cubic-bezier(0.61, 1, 0.88, 1);
  color: #2ff8e7;
  /* opacity: 0; */
  opacity: 1;
  position: relative;
  display: grid;
  grid-template:
    'label .'
    'tag expl';
  align-items: end;
  line-height: 1em;
  grid-gap: 1em;
  gap: 1em;
  overflow: hidden;
  pointer-events: none;
  height: 12.5vw;
}

.data.show {
  opacity: 1;
}

.data > *:nth-child(1) {
  transition-delay: 0s;
  -webkit-transform: matrix(1, 0, 0, 1, 0, 36);
          transform: matrix(1, 0, 0, 1, 0, 36);
}

.data > *:nth-child(2) {
  transition-delay: 0.1s;
  -webkit-transform: matrix(1, 0, 0, 1, 0, 119);
          transform: matrix(1, 0, 0, 1, 0, 119);
}

.data > *:nth-child(3) {
  transition-delay: 0.2s;
  -webkit-transform: matrix(1, 0, 0, 1, 0, 78);
          transform: matrix(1, 0, 0, 1, 0, 78);
}

.data > * {
  transition: -webkit-transform 0.5s cubic-bezier(0.61, 1, 0.88, 1);
  transition: transform 0.5s cubic-bezier(0.61, 1, 0.88, 1);
  transition: transform 0.5s cubic-bezier(0.61, 1, 0.88, 1), -webkit-transform 0.5s cubic-bezier(0.61, 1, 0.88, 1);
}

.data.show > * {
  -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
          transform: matrix(1, 0, 0, 1, 0, 0);
}

.data > div {
  padding-top: 0.5em;
  width: auto;
  grid-area: label;
  position: relative;
  font-size: 1.5vw;
  line-height: 25px;
}

.data > div::after {
  transition: width 0.7s cubic-bezier(0.61, 1, 0.88, 1);
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0vw;
  height: 4px;
  background: black;
}

.data.show > div::after {
  width: 3.5vw;
}

h3 {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 7vw;
  line-height: 0.8em;
  letter-spacing: -0.05em;
}

.data > h3 {
  grid-area: tag;
}

.data > h4 {
  grid-area: expl;
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 1vw;
  line-height: 1.25em;
}

